
.about-holder {
    background: $primePurple;

    .about-twrap{
        width: 315px;
    }

    h3 {
        @include fontSettings($pureWhite, 60px, 600);
        //margin: -85px 0px 10px;
        margin: 40px 0px 10px;
    }

    p {
        @include fontSettings($pureWhite, 22px, 400);
        word-wrap: break-word;
        margin: 0px 0px 10px;
    }

    .personal-wrap{
        height:500px;
    }

    .personal-holder {
        @include dimensions($pureBlack, 450px, 450px, 225px);
        margin: 20px 0px 0px;

        h4 {
            @include fontSettings($pureWhite, 40px, 500);
            margin: 30px 0px 6px; 
        }

        p {
            @include fontSettings($pureWhite, 18px, 400);
            margin: 30px 70px 100px;
            word-wrap: break-word;
        }
    }

    .texthold {
        @include dimensions($darkPurple, 450px, 450px, 225px);
    }

    .leftbutton, .rightbutton{
        color: $pureWhite;
        top: 225px;
    }

    .book{
        width: 300px;
        margin: 60px auto 30px;
    }

    .resbutton{
        @include border($pureWhite, 1px);
        @include dimensions($primePurple, 300px, 75px, 10px);
        @include fontSettings($pureWhite, 22px, 500); 
        margin: 0 auto 50px;
        white-space: normal;
    }
}